*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.person{
    background-color: antiquewhite;
    width: 17rem;
    padding: 1rem;
    margin: 2rem auto;
    border-radius: 0.3rem;
    text-align: center;
    color: tomato;
    transition: all 0.3s linear;

    

}
.person:hover{
    box-shadow: 2px 5px 10px brown;
    cursor: pointer;
}
.person img{
    border-radius: 50%;
    border-radius: 0.1rem solid teal;
}
.person h4{
    text-transform: uppercase;
    margin: 0rem 0;
    letter-spacing: 0.25rem;
    color: rgb(245, 171, 158);
}
.person-list{
    display: flex;

    
}
img{
    width: 40px;
    height: 40px;
    
}
@media screen and (max-width:768px){
    .person-list{
        width: 100%;
        display: block;
    }
}